
.chef-photo {
   border-radius: 4px 4px 4px 200px;
   box-shadow: -16px 16px 0 0 var(--color-primary-light),
     -16px 16px 0 2px var(--color-secondary-dark);
 }
 
 .chef-card {
   display: flex;
   gap: 30px;
 }
 
 .chefs-title {
   position: relative;
   margin: 0 auto 80px;
 }
 
 .chefs-list {
   display: flex;
   gap: 40px 30px;
   flex-wrap: wrap;
 }
 
 .chefs-item {
   flex-basis: calc((100% - 30px) / 2);
 }
 
 .chef-text p {
   margin-bottom: 24px;
 }
 
 /* в последнем абзаце убрать отступ снизу */
 .chef-text p:last-child {
   margin-bottom: 0;
 }
 
 /* псевдоелемент линия на макете в заголовке */
 .chefs-title::before,
 .chefs-title::after {
   position: absolute;
   bottom: 24px;
   width: 352px;
   border: 2px solid var(--color-brand-light);
   content: "";
 }
 
 .chefs-title::after {
   right: 0;
 }
 
 .chefs-title::before {
   left: 0;
 }
 
 .chef-title {
   margin-bottom: 16px;
   font-size: 40px;
   font-weight: 600;
 }