*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  --color-primary-dark: #31261a;
  background-color: var(--color-primary-light);
  font-family: "Poppins, sans-serif";
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  margin: 0;

}


h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

a {
  display: inline-block;
}

address {
  font-style: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}